@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0px;
}

body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0,0,0,0.5);
  color:rgb(255,255,255);
}

/* background img */
.app:before {
  content: ''; /* if anything written it will appear in background */
  background: url('./assets/sunset.jpg') no-repeat  center/cover;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

/* search bar container */
.app .search {
  text-align: center;
  padding: 1rem;
}

/* inside search bar */
.app input {
  /* margin: 3rem auto; */
  padding: 0.7rem 2rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
}

/* text color for placeholder */
::placeholder {
  color: #f8f8f8;
}


.location p{
  font-size: 2rem;
}
.container {
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  justify-content: space-around;
}


/* top part margin above  */

/* .app .top {
  width: 100%;
  margin: -1rem auto;
} */

/* cloud text part */
.app .description {
  position: relative;
  right: -90%;  /* to move on x axis */
  transform-origin: 0 0;
  transform: rotate(269deg);
}

/* bottom part */
.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin:1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}

.bold {
  font-weight: 900;
}







 
